<template>
  <div>
    <b-container fluid>
      <b-row class="sideNav">
        <b-col cols="2" class="gauche">
          <SideNav />
        </b-col>
        <b-col cols="10" class="droite text-left">
          <SmallTopNav />
          <b-container fluid class="corp">
            <b-row class="titre mb-3 mt-5">
              <h1>AjoutCandidat</h1>
            </b-row>
            <b-form @submit="createCandidat">
              <b-row class="formullaireAjout">
                <b-col cols="6" class="sectionGauche">
                  <b-row class="nom">
                    <b-col>
                      <b-form-group label="Votre nom:">
                        <b-form-input v-model="nom" type="text" />
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row class="tel">
                    <b-col>
                      <b-form-group label="Numro de tlphone:">
                        <b-form-input v-model="telephone" type="number" />
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row class="email">
                    <b-col>
                      <b-form-group label="Adresse Courriel:">
                        <b-form-input v-model="courriel" type="email" />
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row class="ville">
                    <b-col>
                      <b-form-group label="Ville: ">
                        <b-form-input v-model="ville" type="text" />
                      </b-form-group>
                    </b-col>
                  </b-row>
                </b-col>
                <b-col cols="6" class="sectionDroite">
                  <b-row class="position">
                    <b-col>
                      <b-form-group label="Stage recherch: ">
                        <b-form-input v-model="position" type="text" />
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row class="ecole">
                    <b-col>
                      <b-form-group label="tablissement scolaire: ">
                        <b-form-input v-model="etablissement" type="text" />
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row class="formation">
                    <b-col>
                      <b-form-group label="Programme suivi">
                        <b-form-select
                          v-model="formationSuivi"
                          :options="formation"
                          class="pl-0"
                        ></b-form-select
                        >
                      </b-form-group>
                    </b-col>
                  </b-row>
                  <b-row class="date">
                    <b-col cols="6">
                      <div>
                        <b-form-group label="Date de dbut du stage">
                          <b-form-datepicker
                            v-model="debut"
                            id="datepickerDebut"
                            class="mb-2"
                          ></b-form-datepicker
                          >
                        </b-form-group>
                      </div>
                    </b-col>
                    <b-col cols="6">
                      <div>
                        <b-form-group label="Date de fin du stage">
                          <b-form-datepicker
                            v-model="fin"
                            id="datepickerFin"
                            class="mb-2"
                          ></b-form-datepicker
                          >
                        </b-form-group>
                      </div>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
              <b-row class="commentaire">
                <b-col cols="12">
                  <b-form-group label="Dcrivez-vous!">
                    <b-form-textarea
                      v-model="description"
                      id="textarea-large"
                      size="lg"
                    ></b-form-textarea>
                  </b-form-group>
                  <b-button type="submit" variant="primary">Soumettre</b-button>
                </b-col>
              </b-row>
            </b-form>
          </b-container>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import SideNav from "../../layout/SideNav";
import SmallTopNav from "../../layout/SmallTopNav";
import json from "../../assets/data.json";
import { v4 as uuidv4 } from "uuid"; //pour donner des Id alatoir
export default {
  name: "AjoutCandidat",
  components: {
    SideNav,
    SmallTopNav,
  },
  data: function() {
    return {
      nom: "",
      telephone: "",
      courriel: "",
      ville: "",
      position: "",
      etablissement: "",
      debut: "",
      fin: "",
      description: "",
      formationSuivi: "",
      formation: [
        { text: "Choisir", value: null },
        "Administrations publiques",
        "Agriculture, foresterie, pche et chasse",
        "Arts, spectacles et loisirs",
        "Commerce de dtail",
        "Commerce de gros",
        "Construction",
        "Dveloppement web",
        "Extraction minire, et extraction de ptrole et de gaz",
        "Fabrication",
        "Finance et assurances",
        "Gestion de socits et dentreprises",
        "Hbergement et services de restauration",
        "Industrie de linformation et industrie culturelle",
        "Services administratifs",
        "gestion des dchets",
        "Services denseignement",
        "Services de restauration et dbit de boisson",
        "Services immobiliers",
        "Services scientifiques et techniques",
        "Services publics",
        "Sant et assistance sociale",
        "Transport et entreposage",
        "Transport par camion",
      ],
      candidats: json.candidats, // aller chercher nos donnes du fichier JSON
    };
  },
  // Crer un nouveau candidat
  methods: {
    createCandidat(e) {
      e.preventDefault();
      const newStage = {
        candidatId: uuidv4(),
        position: this.position,
        nom: this.nom,
        ville: this.ville,
        etablissement: this.etablissement,
        formation: this.formationSuivi,
        debut: this.debut,
        fin: this.fin,
        courriel: this.courriel,
        telephone: this.telephone,
        description: this.description,
        vedette: false,
        valide: false,
        visible: true,
      };
      console.log(newStage);
    },
    ajouterStage() {
      this.stages = [...this.stages, this.newStage];
    },
  },
};
</script>

<style lang="scss"></style>
